define(function () {
  // Russian
  function ending(count, one, couple, more) {
    if (count % 10 < 5 && count % 10 > 0 && count % 100 < 5 || count % 100 > 20) {
      if (count % 10 > 1) {
        return couple;
      }
    } else {
      return more;
    }

    return one;
  }

  return {
    errorLoading: function errorLoading() {
      return '  ';
    },
    inputTooLong: function inputTooLong(args) {
      var overChars = args.input.length - args.maximum;
      var message = ',   ' + overChars + ' ';
      message += ending(overChars, '', 'a', '');
      message += ' ';
      return message;
    },
    inputTooShort: function inputTooShort(args) {
      var remainingChars = args.minimum - args.input.length;
      var message = ',     ' + remainingChars + ' ';
      message += ending(remainingChars, '', 'a', '');
      return message;
    },
    loadingMore: function loadingMore() {
      return ' ';
    },
    maximumSelected: function maximumSelected(args) {
      var message = '     ' + args.maximum + ' ';
      message += ending(args.maximum, '', 'a', '');
      return message;
    },
    noResults: function noResults() {
      return '  ';
    },
    searching: function searching() {
      return '';
    },
    removeAllItems: function removeAllItems() {
      return '  ';
    }
  };
});